import React, { Component } from 'react';
import Weather from './Weather';
import styled from 'styled-components';
import alphaColorBg from '../assets/30_percent_opacity_black.png';

const App = styled.div`
  // background-color: #4694E7;
  background-image: url(${alphaColorBg}), url('https://media.istockphoto.com/vectors/sky-and-clouds-background-sky-and-cloud-with-blue-color-cartoon-vector-id1084416182?k=6&m=1084416182&s=170667a&w=0&h=QuYlwwQlouomtfF0Le4RVveL-VhTVQHwj33bFwBaczI=');
  background-size: cover;
  background-attachment: fixed;
  color: white;
  min-height: 100vh;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-areas:
  'main'
  'hourly'
  'extra'
  'daily';
  @media screen and (min-width: 1024px) {
    grid-template-columns: 1fr 1fr;
    grid-template-areas:
    'main extra'
    'hourly daily';
  }
`;

class Main extends Component {
  render() {
    return (
      <App>
        <Weather />
      </App>
    )
  }
}

export default Main
